.picture {
    text-align: center;
    position: absolute;
  left: 50%;
  transform: translate(-50%);
  top: 170px;
    overflow: hidden;
    width: 530px;
    height: 600px;
    z-index: -10;
  }
  .pictureDisabled {
    pointer-events: none;
  }
  .photo {
    position: absolute;
    width: 530px;
    
    left: 50%;
  transform: translate(-50%);
    top:0px;
    pointer-events: none;
    z-index: -2;
  }
  
  .first {
    
    position: absolute;
    top:213px;
    left: 158px;
     -webkit-transform: 
    perspective(350px) 
    rotateY(5deg)
    rotateX(5deg)
    rotate(-10.5deg)
    translateZ(-35px)
    skew(0deg, 0deg); 
    z-index: -6;
    width: 153px;
    height: 155px;
    
  }
  .second {
    position: absolute;
    top: 412px;
    left: 22px;
    
    transform: 
    perspective(200px)
    rotateY(5deg)
    rotateX(10deg)
    rotate(-30deg)
    skew(5deg, 0deg);
    z-index: -6;
    width: 183px;
    height: 173px;
  }
  .third {
    position: absolute;
    top: 328px;
    left: 368px;
    
    transform: 
    perspective(300px)
    rotateY(-5deg)
    rotateX(40deg)
    rotate(37deg)
    skew(-5deg, -11deg)
    translateZ(-20px);
    z-index: -3;
    width: 132px;
    height: 112px;
  }
  .fourth {
    position: absolute;
    top: 238px;
    left: 322px;
    -webkit-transform: 
    perspective(300px)
    rotateY(-10deg)
    rotateX(5deg)
    rotate(5.5deg)
    skew(0deg, 0deg)
    translateZ(-75px);
    z-index: -4;
    width: 137px;
    height: 135px;
  }
  
  
