
.wrapperSearch {
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.form {
    position: relative;
}
.search {
    width: 516px;
    height: 35px;
    border: 1px solid #cbd5e0;
    outline: none;
    display: inline;
    padding-left: 10px;
    font-family: 'Mukta', sans-serif;
}
@media (max-width: 325px) {
   .search {
    width: 516px;
    height: 65px;
    border: 1px solid #cbd5e0;
    outline: none;
    font-size: 25px;
    padding-left: 10px;
    font-family: 'Mukta', sans-serif;
   }
}
.button {
    font-size: 15px;
    
}
.preloader {
    position: absolute;
    left: 93%;
    top: 7px;
}